import uniNumberBox from '@/components/uni-number-box/uni-number-box.vue'
import kmLi from '../../components/km-li.vue'
export default {
	data() {
		return {
			carNum:0,
			userinfo:{},
			type: '',
			isAgree:'',
			status:0,	//1:待确认；s-0：等待接单；3：运输中	//0：审核中;1:未接单；3:待确定；4：运输中；5：已完成；
			isUpdate:0, //是否是修改订单界面。0：否；1：是
			lookInfoNeedMoney:100,
			cutMoney:0,
			seeMoreIsOpen:0,
			orderId:'',
			order:'',
			url:''
		}
	},
	components: {
		uniNumberBox,
		kmLi
	},
	onLoad(e) {
		/* uni.showLoading({
			title:'加载中...',
			mask:true
		}) */
		var _this = this;
		this.userinfo = uni.getStorageSync('userinfo');
		console.log(e.status);
		this.orderId = e.orderId;
		uni.removeStorage({
			key:'cutMoney',
			success() {
				
			}
		})
		
		/* var url = this.serverURL;
		this.url = this.serverURL;
		uni.request({
			url: url + 'home/index/get_transportation_order',
			method:'POST',
			data:{
				tid: _this.orderId,
				user_id: _this.userinfo.user_id
			},
			success(res) {
				if(res.data.code==100){
					uni.hideLoading();
					var order = res.data.list;
					_this.order = order;
					console.log(order);
					_this.status = parseInt(order.states);
					_this.carNum = order.car_num;
				}
			},
			fail(error) {
				console.log(error)
			}
		}) */
	},
	onShow() {
		uni.showLoading({
			title:'加载中...',
			mask:true
		})
		var _this = this;
		uni.getStorage({
			key:'cutMoney',
			success(res) {
				console.log(res.data);
				_this.cutMoney = res.data * (-1);
			}
		})
		var url = this.serverURL;
		this.url = this.serverURL;
		uni.request({
			url: url + 'home/index/get_transportation_order',
			method:'POST',
			data:{
				tid: _this.orderId,
				user_id: _this.userinfo.user_id
			},
			success(res) {
				if(res.data.code==100){
					uni.hideLoading();
					var order = res.data.list;
					_this.order = order;
					console.log(order);
					_this.status = parseInt(order.states);
					_this.carNum = order.car_num;
				}
			},
			fail(error) {
				console.log(error)
			}
		})
	},
	methods: {
		changeCar(value) {
			this.carNum = value;
		},
		seeMore() {
			var _this = this;
			if(_this.seeMoreIsOpen==0){
				_this.seeMoreIsOpen=1;
			} else {
				_this.seeMoreIsOpen=0;
			}
		},
		makePhoneCall(e) {
			var phone = e.currentTarget.dataset.phone;
			console.log(phone);
			uni.makePhoneCall({
				phoneNumber: phone
			})
		},
		goConfirm(id,status) {
			uni.navigateTo({
				url:'../confirmOrder/confirmOrder?id='+id+"&status="+status
			})
		},
		updateOrder() {
			this.isUpdate = 1;
		},
		confirmUpdate() {
			var url = this.serverURL;
			var _this = this;
			uni.showLoading({
				title:'修改中...',
				mask:true
			})
			uni.request({
				url: url + 'home/release/update_information',
				method:'POST',
				data:{
					id: _this.order.id,
					user_id: _this.order.user_id,
					car_num: _this.carNum
				},
				success(success) {
					console.log(success);
					if(success.data.code==100){
						uni.showToast({
							title:'修改成功',
							mask:true,
							icon:'success',
							success() {
								_this.isUpdate = 0;
								_this.order.car_num = _this.carNum;
								uni.hideLoading();
							}
						})
					}
				},
				fail(fail) {
					console.log(fail);
					uni.showToast({
						title:'修改失败',
						mask:true,
						icon:'none',
						success() {
							_this.isUpdate = 0;
							uni.hideLoading();
						}
					})
				}
			})
			
			
		},
		seeMap(id,status) {
			uni.navigateTo({
				url:'../carMap/carMap?id='+id+"&status="+status
			})
		},
		cancelOrder(){
			var _this = this;
			uni.request({
				url: _this.url + 'home/release/update_information',
				method:'POST',
				data:{
					id:_this.order.id,
					states: 6
				},
				success(res) {
					console.log(res);
					if(res.data.code==100){
						_this.status = 6;
						uni.showToast({
							title:'订单已取消',
							mask:true,
							icon:"success"
						})
					}
				},
				fail(error) {
					console.log(error)
				}
			})
		}
	}
}
